<template>
    <div>
        <p v-if="news > 0">Based on {{ news }} news.</p>
        <div id="donald-trump-chart" />
    </div>
</template>

<script>
import axios from 'axios'
import Plotly from 'plotly.js-dist'

export default {
  async mounted () {
    let { data: chart } = await axios.get('/api/entities?entity=Donald%20Trump')
    chart.map(({ count, distance, word }) => {
      this.chart.x.push(Math.round(distance))
      this.chart.text.push(Math.round(distance))
      this.chart.y.push(word)
      this.chart.marker.color.push(count)
    })

    Plotly.newPlot('donald-trump-chart', [this.chart], this.layout, this.options)

    let { data: count } = await axios.get('/api/news?entity=Donald%20Trump')

    this.news = count
  },

  data: () => ({
    news: 0,
    chart: {
      type: 'bar',
      x: [],
      y: [],
      text: [],
      textposition: 'auto',
      orientation: 'h',
      marker: {
        color: []
      }
    },

    layout: {
      width: 500,
      height: 500,
      showlegend: false,
      hovermode: false,
      font: {
        family: '-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji"',
        size: 14,
        color: '#000000'
      },

      margin: {
        b: 0,
        t: 0,
        pad: 5
      },

      xaxis: {
        fixedrange: true,
        zeroline: false,
        ticks: '',
        showgrid: false
      },

      yaxis: {
        fixedrange: true,
        zeroline: false,
        ticks: '',
        showgrid: false
      }
    },

    options: {
      displayModeBar: false,
      responsive: true
    }
  })
}
</script>
